  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: #62d5b4;
  background: -moz-linear-gradient(-45deg, #62d5b4 0%, #399494 100%);
  background: -webkit-linear-gradient(-45deg, #62d5b4 0%, #399494 100%);
  background: linear-gradient(135deg, #62d5b4 0%, #399494 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#62d5b4', endColorstr='#399494',GradientType=1 );
  padding: 60px;
  overflow: hidden; }
  @media only screen and (max-width: 1023px) {
    .sample {
      padding: 30px; } }
  .sample .sample__title {
    font-size: 5rem;
    color: #fff; }
    @media only screen and (max-width: 1023px) {
      .sample .sample__title {
        font-size: calc(3.5rem*0.75 + .4vw); } }
